import React from "react";
import CardPubli from "../components/Secciones/Card";
import TituloSeccion from "../components/Secciones/TituloSeccion";

import Boton from "../components/Boton";

const ticksAds = [
  "Campañas en 24 hs.",
  "Pago solo por Click.",
  "Relevancia al máximo",
  "No gana el que más paga",
];
const ticksFB = [
  "Mayor alcance",
  "Segmentacion Avanzada",
  "Análisis completo y a tiempo real",
  "Baja inversión",
];
const PublicidadDigital = () => {
  return (
    <div id="publi">
      <div style={{ padding: 30 }}></div>
      <div className="container">
        <div style={{ marginTop: 40 }} className="text-center">
          <TituloSeccion titulo="Publicidad Digital" />
          <div className="row justify-content-center">
            <div className="cardPublicidad hvr-grow">
              <CardPubli ticks={ticksAds} image="icono" />
            </div>
            <div className="cardPublicidad hvr-grow">
              <CardPubli ticks={ticksFB} fb={true} image="fb" image2="ig" />
            </div>
          </div>
          <div className="text-center hvr-grow botonNazi">
            <Boton
              info="Publicidad"
              background="var(--amarillo)"
              color="var(--azul)"
            >
              Me Interesa
            </Boton>
          </div>
          <br></br>
        </div>
      </div>
    </div>
  );
};
export default PublicidadDigital;
