import React from "react";
import TituloSeccion from "../components/Secciones/TituloSeccion";
import SubituloSeccion from "../components/Secciones/SubtituloSeccion";
import ImagenSeccion from "../components/Secciones/ImagenSeccion";
import Collaspe from "../components/Secciones/Collapse";
import Boton from "../components/Boton";
import background from "../probando2.png";
const desc = "  ";
const publicidad = (
  <p>
    El futuro digital es hoy. Vivimos en un mundo inmerso en las redes sociales,
    en la inmediación de la información y en la interrelación sin fronteras
    gracias a Internet.
    <br />
    Es importante tener una buena estrategia de publicidad en redes sociales,
    facebook es la plataforma mas importante hoy en dia. Una buena planificacion
    hara que llegues a tu cliente potencial y de esta forma conseguir mas
    conversiones. Dejalo en manos de expertos, dejalo en manos de Flobella.
  </p>
);
const gestion = (
  <p>
    Mejorá el posicionamiento de tu marca y/o empresa, el engagement, tu
    branding, adquisición de nuevos clientes y rentabilizá tus redes sociales a
    través de nuestro servicio de gestión y community manager
  </p>
);
const styles = {
  backgroundImage: `url(${require("../probando2.png")})`,
  backgroundPosition: "center",
  backgroundSize: "cover",
  backgroundRepeat: "no-repeat",
  width: "50%",
  position: "absolute",
  height: "50%",
  top: "60%",
  right: "100%",
  borderRadius: "100%",
};
const RedesSociales = () => {
  return (
    <div id="redes-sociales">
      <div style={{ padding: 35 }}></div>
      <div className="container ">
        <div className="row">
          <div className="col-md-6 inforedes">
            <SubituloSeccion subtitulo="Potenciamos tu Marca" />
            <TituloSeccion titulo="Redes Sociales" />
            <div className="divnosotros">
              <Collaspe desc={gestion} boton="Gestion de Redes Sociales" />
              <Collaspe
                valor={true}
                desc={publicidad}
                boton="Publicidad de Redes Sociales"
              />
            </div>
            <br></br>
            <div style={styles}></div>
          </div>
          <div className="col-md-6">
            <ImagenSeccion img="redes" />
          </div>
        </div>
        <div className="text-center hvr-grow botonNazi">
          <Boton info="Redes" background="#fcce06" color="var(--azul)">
            Me Interesa
          </Boton>
        </div>
      </div>
    </div>
  );
};

export default RedesSociales;
