import React from "react";
import Header from "../components/Header";
import Footer from "../components/Footer";
import HeaderMobile from "../components/HeaderMobile";
import Boton from "../components/Boton";
const Error404 = () => {
  return (
    <div>
      <div>
        <Header />
      </div>
      <div>
        <HeaderMobile />
      </div>
      <div className="text-center" style={{ padding: 90 }}>
        <h1
          style={{
            fontFamily: "var(--alieronBlack",
            color: "var(--azul)",
            fontSize: "50px",
          }}
        >
          404
        </h1>
        <h2 tyle={{ fontFamily: "var(--alieron", color: "var(--azul)" }}>
          Pagina no encontrada
        </h2>
        <button
          onClick={() => {
            window.location.href = "https://flobellamedia.site/";
          }}
          className="boton"
          style={{ color: "var(--azul)", backgroundColor: "var(--amarillo)" }}
        >
          Volver
        </button>
      </div>
      <div>
        <Footer />
      </div>
    </div>
  );
};
export default Error404;
