import React from "react";
import { BrowserRouter as Router } from "react-router-dom";

import AnchorLink from "react-anchor-link-smooth-scroll";
import "./etiqueta.css";
const items = [
  {
    url: "/#nosotros",
    name: "Inicio"
  },
  {
    url: "#redes-sociales",
    name: "Redes Sociales"
  },
  {
    url: "#adsgoogle",
    name: "Google Ads"
  },
  {
    url: "#diseño-web",
    name: "Diseño Web"
  },
  {
    url: "publi",
    name: "Publicidad Digital"
  }
];

const Etiqueta = () => {
  return (
    <Router>
      {items.map(item => {
        return (
          <AnchorLink className="link" href={item.url}>
            {item.name}
          </AnchorLink>
        );
      })}
    </Router>
  );
};
export default Etiqueta;
