import React from "react";
import TituloSeccion from "../components/Secciones/TituloSeccion";
import SubituloSeccion from "../components/Secciones/SubtituloSeccion";
import ImagenSeccion from "../components/Secciones/ImagenSeccion";
import DescripcionSeccion from "../components/Secciones/DescripcionSeccion";
import Boton from "../components/Boton";

const parafo1 = (
  <p>
    {" "}
    Es importante aparecer en las <strong>primeras posiciones</strong> de los
    principales buscadores, tu cliente te está buscando y tu negocio tiene que
    ser la <strong>primera opción.</strong>
  </p>
);
const parrafo2 = (
  <p>
    Para ser relevantes en los buscadores necesitar estar bien posicionados en
    ellos, es por eso que nos ocupamos de
    <br />
    <strong> diseñar, planificar y optimizar </strong>sus campañas de PPC, para
    obtener el mayor rendimiento de tu inversión
  </p>
);

const GoogleAds = () => {
  return (
    <div id="adsgoogle">
      <div style={{ padding: 35 }}></div>
      <div className="container-fluid">
        <div className="row align-items-start">
          <div className="col-md-6">
            <ImagenSeccion img="imagenGoogle" />
          </div>
          <div className="col-md-6 info">
            <TituloSeccion titulo="Google Ads " />
            <SubituloSeccion subtitulo="Aparece en los Buscadores" />

            <DescripcionSeccion desc={parafo1} parrafo1={parrafo2} />

            <img className="background" src="/img/probando.png"></img>
          </div>
        </div>
        <div className="text-center hvr-grow botonNazi">
          <Boton
            info="Google Ads"
            background="var(--amarillo)"
            color="var(--azul)"
          >
            Me Interesa
          </Boton>
        </div>
        <br></br>
      </div>
    </div>
  );
};
export default GoogleAds;
