import React from "react";
import TituloSeccion from "../components/Secciones/TituloSeccion";
import SubituloSeccion from "../components/Secciones/SubtituloSeccion";
import ImagenSeccion from "../components/Secciones/ImagenSeccion";
import Boton from "../components/Boton";

const DiseñoWeb = () => {
  return (
    <div id="nosotros">
      <div style={{ padding: 35 }}></div>
      <div className="container">
        <div className="row">
          <div className="col-md-6 inforedes">
            <TituloSeccion titulo="Diseño Web" />
            <SubituloSeccion subtitulo="Diseñamos tu Web a medida" />

            <div
              style={{ marginTop: 30 }}
              className="row justify-content-space-between text-center iconosDiseno"
            >
              <div className="col-5 ">
                <div>
                  <img width="70px" src={`img/icon/ecomerce.png`}></img>
                </div>
                <div className="text-center">
                  <p className="textIcon">E-Commerce</p>
                </div>
              </div>
              <div className="col-4">
                <div>
                  <img width="70px" src={`img/icon/institucionales.png`}></img>
                </div>
                <div className="text-center">
                  <p className="textIcon">Institucionales</p>
                </div>
              </div>
            </div>
            <div style={{ marginLeft: 50 }} className="row justify-content-end">
              <div className="diseno">
                <img src="/img/tick.jpg" width="8%" />
                Integracion con Mercado Libre
              </div>
              <div className="diseno">
                <img src="/img/tick.jpg" width="8%" />
                Integracion de Mercado Pago
              </div>
              <div className="diseno">
                <img src="/img/tick.jpg" width="8%" />
                100% Autoadministrable
              </div>
              <div className="diseno">
                <img src="/img/tick.jpg" width="8%" />
                Mordena
              </div>
            </div>
          </div>
          <div className="col-md-6">
            <ImagenSeccion img="web" />
          </div>
          <div className="text-center hvr-grow botonNazi">
            <Boton info="web" background="var(--amarillo)" color="var(--azul)">
              Me Interesa
            </Boton>
          </div>
        </div>
      </div>
    </div>
  );
};
export default DiseñoWeb;
