import React from "react";
import TituloSeccion from "../components/Secciones/TituloSeccion";
import SubituloSeccion from "../components/Secciones/SubtituloSeccion";
import ImagenSeccion from "../components/Secciones/ImagenSeccion";
import DescripcionSeccion from "../components/Secciones/DescripcionSeccion";
import Iconos from "../components/Secciones/Iconos";

const iconos = [
  {
    img: "investigacion",
    text: "Investigacion",
  },
  {
    img: "idea",
    text: "Idea",
  },
  {
    img: "planeacion",
    text: "Plneacion",
  },
  {
    img: "analisis",
    text: "Analisis y Optimizacion",
  },
  {
    img: "resultados",
    text: "Resultados",
  },
];

const parrafo = (
  <p>
    Hoy en fundamental Digitalizar tu negocio y
    <strong> planificar una estrategia publicitaria</strong> en las redes
    sociales, Ya que tu cliente potencial pasa gran parte de su dia en estas
    plataformas. La competencia en el negocio es cada vez mayor y solo
    <strong> siendo diferente</strong> lograras mejores resultados, Para eso
    nació flobella, <strong> Suma un aliado estratégico</strong>
  </p>
);
const Nosotros = () => {
  return (
    <div id="nosotros">
      <div style={{ padding: 35 }}></div>
      <div className="container-fluid">
        <div className="row align-items-start justify-content-center">
          <div className="col-md-6">
            <ImagenSeccion img="nosotros" />
          </div>
          <div className="col-md-6 info">
            <SubituloSeccion subtitulo="Tu Agencia de Marketing" />
            <TituloSeccion titulo="Suma un Aliado " />
            <TituloSeccion titulo="Estratégico " />
            <DescripcionSeccion desc={parrafo} />
            <img className="background" src="/img/probando.png"></img>
          </div>
        </div>

        <div className="row text-center justify-content-center">
          {iconos.map((icon) => (
            <div className="col-md-2 col-sm-4 col-12 hvr-grow">
              <Iconos text={icon.text} icon={icon.img} />
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};
export default Nosotros;
