import React from "react";
import Header from "./components/Header";
import Nosotros from "./Views/Nosotros";
import RedesSociales from "./Views/RedesSociales";
import GoogleAds from "./Views/GoogleAds";
import PublicidadDigital from "./Views/PublidadDigital";
import DiseñoWeb from "./Views/DiseñoWeb";
import Footer from "./components/Footer";
import HeaderMobile from "./components/HeaderMobile";
import Wpp from "./components/Whatsapp/Wpp";

const Router = () => {
  return (
    <div>
      <div>
        <Header />
      </div>
      <div>
        <HeaderMobile />
      </div>

      <div id="#nosotros">
        <Nosotros />
      </div>
      <div id="redes-sociales">
        <div>
          <RedesSociales />
        </div>
      </div>

      <div>
        <GoogleAds />
      </div>

      <div id="diseño-web">
        <DiseñoWeb />
      </div>
      <div>
        <PublicidadDigital />
      </div>
      <div>
        <Footer />
      </div>

      <Wpp />
    </div>
  );
};
export default Router;
